import React, { useState, useEffect } from 'react'
import { useTranslation } from "react-i18next";

function View() {
  const { t } = useTranslation();

  const [creations, setCreations] = useState(null)
  useEffect(() => {
    fetch("http://localhost:4000/creations")
      .then(res => res.json())
      .then(result => {
        setCreations(result)
      })
  }, [])

  const styleCreationRow = {
    border: 'solid 1px black',
  }

  return (
    <>
      <h1>{t("nav.view")}</h1>
      
      <div id='creations'>
        { creations && creations.map((creation, index) => {
            return (
              <div style={styleCreationRow} key={index}>
                { creation.videoUrl }
              </div>
            )
        })}
      </div>
    </>
  )
}

export default View
