import React from 'react'
import Input from './Input'
import Integer from './Integer'
import Textarea from './Textarea'
import Select from './Select'
import RadioButtons from './RadioButtons'
import CheckboxGroup from './CheckboxGroup'
import DatePicker from './DatePicker'
import Image from './Image'

function FormikControl(props) {
    // this component decides which form field has to be rendered based on control prop
    const { control, ...rest } = props
    switch(control){
        case 'input': return <Input {...rest} />
        case 'integer': return <Integer {...rest} />
        case 'textarea': return <Textarea {...rest} />
        case 'select': return <Select {...rest} />
        case 'radio': return <RadioButtons {...rest} />
        case 'checkbox': return <CheckboxGroup {...rest} />
        case 'date': return <DatePicker {...rest} />
        case 'image': return <Image {...rest} />
        default: return null
    }
}

export default FormikControl
