import React, { useState, useEffect } from 'react'
// routing
import { Routes, Route, NavLink } from 'react-router-dom'

// pages
import Home from './components/pages/Home';
import Create from './components/pages/Create';
import Editor from './components/pages/Editor';
import View from './components/pages/View';
import Settings from './components/pages/Settings';
import Help from './components/pages/Help';
import NotFound from './components/pages/NotFound';

// modals
import Loading from './components/modals/Loading';

// animtation
import { motion } from 'framer-motion'

// localization
import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import { useTranslation } from 'react-i18next';
import translationEN from './translations/en/translation.json';
import translationNL from './translations/nl/translation.json';
import translationDE from './translations/de/translation.json';

function App() {

  const [locale, setLocale] = useState('nl')
  // TODO: move localization state to global state
  const changeLocale = (locale) => {
    setLocale(locale)
  }

  useEffect(() => {
    i18n
      .use(initReactI18next)
      .init({
          resources: {
              en: {
                  translation: translationEN
              },
              nl: {
                  translation: translationNL
              },
              de: {
                  translation: translationDE
              }
          },
          fallbackLng: 'en',
          debug: true,
          lng: locale,
          interpolation: {
              escapeValue: false,
          }
      });
  }, [locale])

  // styles
  const styleLocalization = {
    position: 'absolute',
    top: 0,
    right: 0,
    padding: '1.5rem',
    zIndex: 9999999999
  }

  const styleNav = {
    height: '100vh',
    width: '20vw',
    borderRight: 'solid 4px black',
    padding: '4rem'

  }

  const styleContent = {
    position: 'absolute',
    top: 0,
    left: '20vw',
    height: '100vh',
    width: '80vw',
    padding: '4rem'
  }

  const { t } = useTranslation();

  // TODO: create robust subdomain whitelabeling system for switching assets
  const [whitelabel, setWhitelabel] = useState('stencil')
  useEffect(() => {
    if(window.location.hostname.split('.').length > 1){
      setWhitelabel(window.location.hostname.split('.')[0])
    }
  }, [])
  
  let [loading, setLoading] = useState(false);

  return (
    <>
      <div id='navigation' style={styleNav}>
        <ul>
          <li>
            <NavLink to='/'>
              <div style={{ fontSize: '3.5rem', textTransform: 'capitalize' }}>{whitelabel}</div>
            </NavLink>
          </li>
          <li><NavLink to='/create'>{t('nav.create')}</NavLink></li>
          <li><NavLink to='/view'>{t('nav.view')}</NavLink></li>
          <li><NavLink to='/settings'>{t('nav.settings')}</NavLink></li>
          <li><NavLink to='/help'>{t('nav.help')}</NavLink></li>
          <li onClick={()=>{alert('Logged out')}}>{t('nav.logout')}</li>
        </ul>
      </div>

      <div style={styleContent}>
        <Routes>
          <Route path='/' element={<Home/>}></Route>
          <Route path='/create' element={<Create/>}></Route>
          <Route path='/editor' element={<Editor/>}></Route>
          <Route path='/view' element={<View/>}></Route>
          <Route path='/settings' element={<Settings setLoading={setLoading} />}></Route>
          <Route path='/help' element={<Help/>}></Route>
          <Route path='/test' component={<NotFound/>}></Route>
        </Routes>
      </div>

      <motion.div 
        style={styleLocalization}
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ duration: 1 }}
      >
        <ul>
          <li className={ locale === 'nl' ? 'active' : undefined } onClick={()=>{changeLocale('nl')}}>NL</li>
          <li className={ locale === 'de' ? 'active': undefined } onClick={()=>{changeLocale('de')}}>DE</li>
          <li className={ locale === 'en' ? 'active' : undefined } onClick={()=>{changeLocale('en')}}>EN</li>
        </ul>
      </motion.div>

      { loading && <Loading loading={loading} setLoading={setLoading} />}
    </>
  );
}

export default App;
