import React, { useState, useEffect } from 'react'
import { useTranslation } from "react-i18next"
import { useQuery } from 'react-query'
import axios from 'axios';

function Settings() {

  const { t } = useTranslation();
  const [settings, setSettings] = useState(null)
  const [error, setError] = useState(null)
  const [isLoading, setIsLoading] = useState(true)


  useEffect(() => {
    axios.get('http://localhost:4000/settings', {timeout: 20000})
    .then((res) => {
      setSettings(res.data)
      setIsLoading(false)
    })
    .catch((error) => {
      setError(error)
      setIsLoading(false)
    })
  }, [])
  

  const styleIdentity = {
    marginTop: '30px'
  }

  const styleColorSettingPreview = {
    width: '20px',
    height: '20px',
    borderRadius: '50px',
    marginRight: '10px'
  }

  const styleColorRow = {
    display: 'flex',
    margin: '10px 0'
  }

  return (
    <div>
      <h1>{t("nav.settings")}</h1>
      { error && <p>{ error.message }</p> }
      { isLoading 
        ? <h2>loading...</h2>
        : <div id='identities'>
          { settings?.identities.map((identity, index) => {
              return (
                <div style={styleIdentity} key={index}>
                  <h2>{ identity.name }</h2>
                    <div style={styleColorRow}>
                      <div style={{ ...styleColorSettingPreview, background: identity.color1 }}></div>
                      <div>{ identity.color1 }</div>
                    </div>
                    <div style={styleColorRow}>
                      <div style={{ ...styleColorSettingPreview, background: identity.color2 }}></div>
                      <div>{ identity.color2 }</div>
                    </div>
                    <div style={styleColorRow}>
                      <div style={{ ...styleColorSettingPreview, background: identity.color3 }}></div>
                      <div>{ identity.color3 }</div>
                    </div>
                </div>
              )
          })}
        </div>
      }
    </div>
  )
}

export default Settings