import React from 'react'
import { useTranslation } from "react-i18next";

import { Formik, Form } from "formik";
import FormikControl from '../formik/formikController/FormikControl';
import * as Yup from 'yup'

function Create() {
  const { t } = useTranslation();

  const initialValues = {
    field1: '',
    field2: ''
  }
  const validationSchema = Yup.object({
    field1: Yup.string().required(t('real_estate_global.error_message_required')),
    field2: Yup.string().required(t('real_estate_global.error_message_required'))
  })

  const onSubmit = () => {
    alert('submit')
  }

  return (
    <>
      <h1>stencil editor</h1>

      <div>
        <Formik 
            initialValues={initialValues} 
            validationSchema={validationSchema} 
            onSubmit={onSubmit}
        >
          {
            formik => (
              <Form>
                  <div id="creation_label">
                      <FormikControl control='input' label='Input 1' name='input1' />
                      <FormikControl control='input' label='Input 2' name='input2' />
                  </div>
                  <button style={{border: 'solid 3px black', cursor: 'pointer'}} className="btn btn-dpm" type="submit">Create video</button>
              </Form>
            )
          }
        </Formik>
      </div>
    </>
  )
}

export default Create
