import React, { useState, useEffect } from 'react'

import {ErrorMessage } from "formik";
import TextError from './TextError';

function ImagePicker(props) {
    const { formProps, label, name, setFieldValue, ...rest } = props
    const [fileName, setFileName] = useState(null)
    const fieldName = name

    return (
        <div className='form-control'>
            <label>{label}</label>
            <label className="custom-file-upload" style={{display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                <input style={{display: 'none'}} className="custom-file-upload" name={name} type="file" accept="image/png, image/jpeg" 
                    onChange={(e) => {
                        setFileName(e.currentTarget.files[0].name)
                        formProps.setFieldValue(fieldName, e.currentTarget.files[0].name)
                    }}
                />
                <div style={{display: 'flex', alignItems: 'center', justifyContent: 'space-between'}}>
                    <svg id="Icons_Folders_ic-folder-simple" data-name="Icons / Folders / ic-folder-simple" xmlns="http://www.w3.org/2000/svg" width="31" height="31" viewBox="0 0 31 31">
                        <rect id="Rectangle_260" data-name="Rectangle 260" width="31" height="31" fill="none"/>
                        <g id="ic-folder-simple" transform="translate(3.074 5.489)">
                        <path id="Path_200" data-name="Path 200" d="M3,7.6H25.268a2.583,2.583,0,0,1,2.583,2.583v10.85a2.583,2.583,0,0,1-2.583,2.583H5.583A2.583,2.583,0,0,1,3,21.033Z" transform="translate(-3 -3.596)" fill="none" stroke="#000" strokeLinecap="round" strokeLinejoin="round" strokeWidth="1.5"/>
                        <path id="Path_201" data-name="Path 201" d="M3,12.508V5.65A1.188,1.188,0,0,1,4.214,4.5h11.87a1.292,1.292,0,0,1,.865.336L19.068,8.5" transform="translate(-3 -4.5)" fill="none" stroke="#000" strokeLinecap="round" strokeLinejoin="round" strokeWidth="1.5"/>
                        </g>
                    </svg>
                    <p style={{fontSize: '13px', fontWeight: 500, paddingTop: '20px', paddingLeft: '15px' }}>{fileName}</p>
                </div>
                <div style={{display: 'flex', alignItems: 'center'}}>
                    <svg style={{marginLeft: '15px'}} id="Icons_Actions_ic-actions-search" data-name="Icons / Actions / ic-actions-search" xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
                        <rect id="Rectangle_157" data-name="Rectangle 157" width="24" height="24" fill="none"/>
                        <g id="ic-actions-search" transform="translate(-0.41 0.4)">
                            <circle id="Ellipse_12" data-name="Ellipse 12" cx="7" cy="7" r="7" transform="translate(6.79 3.15)" fill="none" stroke="#000" strokeLinecap="round" strokeLinejoin="bevel" strokeWidth="1.5"/>
                            <line id="Line_50" data-name="Line 50" x1="4.88" y2="4.88" transform="translate(4.03 15.17)" fill="none" stroke="#000" strokeLinecap="round" strokeLinejoin="bevel" strokeWidth="1.5"/>
                        </g>
                    </svg>
                </div>
            </label>
            <ErrorMessage name={name} component={TextError} />
        </div>
  )
}

export default ImagePicker;
