import React, { useState, useEffect } from 'react'
import { useTranslation } from "react-i18next";
import { Link } from 'react-router-dom'

function Create() {
  const { t } = useTranslation();

  const [stencils, setStencils] = useState(null)
  useEffect(() => {
    fetch('http://localhost:4000/stencils')
      .then(res => res.json())
      .then(result => {
        setStencils(result)
      })
  }, [])

  const styleStencils = {
    display: 'flex',
    flexWrap: 'wrap',
    overflowY: 'scroll',
    height: '80vh',
    gap: '12px'
  }

  const styleStencil = {
    width: '250px',
    height: '250px',
    border: 'solid 2px black'
  }

  return (
    <>
      <h1>{t("nav.create")}</h1>

      <div style={styleStencils} id='stencils'>
        { stencils && stencils.map((stencil, index) => {
            return (
              <div onClick={()=>{alert(`Open template ${stencil.title}`)}} style={styleStencil} key={index}>
                <Link to='/editor'>
                  { stencil.title }
                </Link>
              </div>
            )
        })}
      </div>
    </>
  )
}

export default Create